<template>
  <div id="vbar" style="width: 100%;height:100%;" >
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  props: {
    videoInfo: {
      type: Object
    }
  },
  data() {
    return {
      myChart: {},
    option : {
        // title: {
        //     text: '',
        //     subtext: ''
        // },
        tooltip: {
            trigger: 'axis',
            axisPointer: {
                type: 'shadow'
            }
        },
        title: {
        left: 'center',
        text: '',
    },
        color: ['#65B581', '#FFCE34', '#FD665F'],
        legend: {
            data: ['']
        },
        grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
        },
        xAxis: {
            type: 'value',
            boundaryGap: [0, 0.01]
        },
        yAxis: {
            type: 'category',
            data: ['', '', '','',  '']
        },
    toolbox: {
        feature: {
            dataZoom: {
                yAxisIndex: 'none'
            },
            restore: {},
            saveAsImage: {}
        }
    },
        series: [
            {
                type: 'bar',
                symbol: 'none',
            // sampling: 'lttb',
            // itemStyle: {
            //     color: 'rgb(255, 70, 131)'
            // },
                data: []
            },

        ]
    }
    };
  },
  // 
  methods: {
    chartInit() {
      this.myChart = echarts.init(document.getElementById("vbar"));
      return this
    },
    setcharOption(option) {
      this.myChart.setOption(option);
      return this
    },
    setOption() {
      if(!this.videoInfo.stat){
        return []
      }
      let option = {
        color: ['#80FFA5', '#00DDFF', '#37A2FF', '#FF0087', '#FFBF00'],
        data: [           
            this.videoInfo.stat.reply,
            this.videoInfo.stat.favorite,
            this.videoInfo.stat.coin,
            this.videoInfo.stat.like,           
            this.videoInfo.stat.view,   
            
        ]
      };
      return option.data;
    }
  },
  mounted() {
    this.chartInit();
    this.setcharOption(this.option)
    this.setcharOption(this.setOption());

      },

  watch: {
    videoInfo: {
      handler: function() {

        this.setcharOption({
            series: [
                {
                // 
                data: this.setOption()
                }
            ]
            })
      }
    }
  }
  // 
};
</script>

<style>
</style>


